@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
.headerVideo{
    height: 100%;
    width: 100%;   
    object-fit: cover;
}
.headerContent{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.15);
    position: absolute;
    top: 0;
    padding: 3% 10%;  
}
.header{
    position: relative;
    width: 100%;
    height: 40vw;
    padding: 0;
    text-align: left;
}
.logo{
    width: 13vw;
    margin-left: -1.5%;
}
.headerP{
    margin-top: 10%;
    font-size: 1.5vw;
    color: white;
    font-weight: 400;
}
.headerH1{
    font-size: 4vw;
    color: white;
    font-weight: 700;
}
.headerContent img:hover{
    width: 13.5vw;
    transition: 0.2s;

}
.logoContainer{
    width: 20vw;
    height: 10vw;
}