.section1{
    width: 100%;
    height: 32vw;
    background-color: rgb(238, 238, 238);
    display: flex;
    align-items: center;
    position: relative;
    
}
.img{   
    position: absolute;
    width: 50%;
    max-width: 100%;
    left: 100%;
    transition: 0.7s;
}

.section1Container h2{
    font-size: 5vw;
    font-weight: 700;
}
.section1Container h3{
    font-size: 1.5vw;
    font-weight: 700;

}
.section1Container p{
    font-size: 1.2vw;
    font-weight: 700;
}
.section1Container{
    position: absolute;
    right: -100%;
    display: flex;
    flex-direction: column;
    width: 50%;
    transition: 0.7s;
}